		var tab_job_applicant = this.edit_view_tab.find( '#tab_job_applicant' );

		var tab_job_applicant_column1 = tab_job_applicant.find( '.first-column' );

		this.edit_view_tabs[0] = [];

		this.edit_view_tabs[0].push( tab_job_applicant_column1 );

		// Status
		var form_item_input = Global.loadWidgetByName( FormItemType.COMBO_BOX );
