	onFormItemChange( target, doNotValidate ) {
		this.setIsChanged( target );
		this.setMassEditingFieldsWhenFormChange( target );
		var key = target.getField();

		this.current_edit_record[key] = target.getValue();

		switch ( key ) {

			case 'country':
				var widget = this.edit_view_ui_dic['province'];
				widget.setValue( null );
				break;

			case 'identification_country':
				widget = this.edit_view_ui_dic['identification_province'];
				widget.setValue( null );
				break;
		}

		if ( key == 'country' ) {
			this.eSetProvince( this.edit_view_ui_dic['country'].getValue(), true );
			this.clearErrorTips();
			this.setEditMenu();
			return;
		}

		if ( key === 'identification_country' ) {
			this.iSetProvince( this.edit_view_ui_dic['identification_country'].getValue(), true );
			this.clearErrorTips();
			this.setEditMenu();
			return;
		}

		if ( !doNotValidate ) {
			this.validate();
		}
	}

	setCurrentEditRecordData() {

		//Set current edit record data to all widgets
		//Set current edit record data to all widgets
		for ( var key in this.current_edit_record ) {
			var widget = this.edit_view_ui_dic[key];
			if ( Global.isSet( widget ) ) {
				switch ( key ) {
					case 'country': //popular case
						this.setCountryValue( widget, key );
						break;
					case 'identification_country':
						if ( !this.current_edit_record['identification_province'] ) {
							this.iSetProvince( this.current_edit_record[key], true );
						} else {
							this.iSetProvince( this.current_edit_record[key] );
						}
						widget.setValue( this.current_edit_record[key] );
						break;
					default:
						widget.setValue( this.current_edit_record[key] );
						break;
				}

			}
		}

		this.collectUIDataToCurrentEditRecord();
		this.setEditViewDataDone();
	}

	onSetSearchFilterFinished() {

		if ( this.search_panel.getSelectTabIndex() === 1 ) {
			var combo = this.adv_search_field_ui_dic['country'];
			var select_value = combo.getValue();
			this.setProvince( select_value );
		}
	}

	onBuildAdvUIFinished() {

		this.adv_search_field_ui_dic['country'].change( $.proxy( function() {
			var combo = this.adv_search_field_ui_dic['country'];
			var selectVal = combo.getValue();

			this.setProvince( selectVal );

			this.adv_search_field_ui_dic['province'].setValue( null );

		}, this ) );
	}

	setProvince( val, m ) {
		var $this = this;

		if ( !val || val === '-1' || val === '0' ) {
			$this.province_array = [];
			this.adv_search_field_ui_dic['province'].setSourceData( [] );
		} else {

			this.company_api.getOptions( 'province', val, {
				onResult: function( res ) {
					res = res.getResult();
					if ( !res ) {
						res = [];
					}

					$this.province_array = Global.buildRecordArray( res );
					$this.adv_search_field_ui_dic['province'].setSourceData( $this.province_array );

				}
			} );
		}
	}

	eSetProvince( val, refresh ) {
		var $this = this;
		var province_widget = $this.edit_view_ui_dic['province'];

		if ( !val || val === '-1' || val === '0' ) {
			$this.e_province_array = [];
			province_widget.setSourceData( [] );
		} else {
			this.company_api.getOptions( 'province', val, {
				onResult: function( res ) {
					res = res.getResult();
					if ( !res ) {
						res = [];
					}
					$this.e_province_array = Global.buildRecordArray( res );
					if ( refresh && $this.e_province_array.length > 0 ) {
						$this.current_edit_record.province = $this.e_province_array[0].value;
						province_widget.setValue( $this.current_edit_record.province );
					}
					province_widget.setSourceData( $this.e_province_array );

				}
			} );
		}
	}

	iSetProvince( val, refresh ) {
		var $this = this;
		var province_widget = $this.edit_view_ui_dic['identification_province'];

		if ( !val || val === '-1' || val === '0' ) {
			$this.i_province_array = [];
			province_widget.setSourceData( [] );
		} else {
			this.company_api.getOptions( 'province', val, {
				onResult: function( res ) {
					res = res.getResult();
					if ( !res ) {
						res = [];
					}

					$this.i_province_array = Global.buildRecordArray( res );
					if ( refresh && $this.i_province_array.length > 0 ) {
						$this.current_edit_record.identification_province = $this.i_province_array[0].value;
						province_widget.setValue( $this.current_edit_record.identification_province );
					}
					province_widget.setSourceData( $this.i_province_array );

				}
			} );
		}
	}

