	onMapClick() {
		// only trigger map load in specific product editions.
		if ( ( Global.getProductEdition() >= 15 ) ) {
			this.is_viewing = false;
			ProgressBar.showProgressBar();
			var data = {
				filter_columns: {
					id: true,
					first_name: true,
					last_name: true,
					address1: true,
					address2: true,
					city: true,
					province: true,
					country: true,
					postal_code: true,
					latitude: true,
					longitude: true
				}
			};
			var ids = this.getGridSelectIdArray();

			data.filter_data = Global.convertLayoutFilterToAPIFilter( this.select_layout );
			if ( ids.length > 0 ) {
				data.filter_data.id = ids;
			}

			var cells = this.api.getJobApplicant( data, { async: false } ).getResult();
			if ( !this.is_mass_editing ) {
				import( /* webpackChunkName: "leaflet-timetrex" */ '@/framework/leaflet/leaflet-timetrex' ).then(( module )=>{
					var processed_data_for_map = module.TTConvertMapData.processBasicFromGenericViewController( cells );
					IndexViewController.openEditView( this, 'Map', processed_data_for_map );
				}).catch( Global.importErrorHandler );
			}
		}
	}

	openEditView( id ) {
		if ( typeof id == 'undefined' && this.edit_only_mode ) {
			this.onCancelClick( true, true );
		} else if ( this.edit_only_mode && id ) {
			var $this = this;
			this.initOptions( function() {
				if ( !$this.edit_view ) {
					$this.initEditViewUI( $this.viewId, $this.edit_view_tpl );
				}
				var filter_data = { filter_data: { id: id } };
				$this.api.getJobApplicant( filter_data, {
					onResult: function( result ) {
						result = result.getResult()[0];
						if ( !result ) {
							TAlertManager.showAlert( $.i18n._( 'Invalid job applicant' ) );
							$this.onCancelClick();
						} else {
							// Waiting for the TTAPI.API returns data to set the current edit record.
							$this.current_edit_record = result;
							$this.initEditView();
						}
					}
				} );
			} );
		} else {
			if ( !this.edit_view ) {
				this.initEditViewUI( this.viewId, this.edit_view_tpl );
			}
		}
	}

