	initSubApplicationView() {
		var $this = this;

		if ( !this.current_edit_record.id ) {
			TTPromise.resolve( 'BaseViewController', 'onTabShow' ); //Since search() isn't called in this case, and we just display the "Please Save This Record ..." message, resolve the promise.
			return;
		}

		if ( this.sub_application_view_controller ) {
			this.sub_application_view_controller.buildContextMenu( true );
			this.sub_application_view_controller.setDefaultMenu();
			$this.sub_application_view_controller.parent_value = $this.current_edit_record.id;
			$this.sub_application_view_controller.parent_edit_record = $this.current_edit_record;
			$this.sub_application_view_controller.initData();
			return;
		}

		TTPromise.add( 'initSubApplicationView', 'loadscript' );
		Global.loadScript( 'views/hr/recruitment/JobApplicationViewController.js', function() {
			var tab_applications = $this.edit_view_tab.find( '#tab_applications' );
			var firstColumn = tab_applications.find( '.first-column-sub-view' );
			Global.trackView( 'Sub' + 'JobApplication' + 'View' );
			JobApplicationViewController.loadSubView( firstColumn, beforeLoadView, afterLoadView );

		} );

		function beforeLoadView() {

		}

		function afterLoadView( subViewController ) {
			$this.sub_application_view_controller = subViewController;
			$this.sub_application_view_controller.parent_key = 'job_applicant_id';
			$this.sub_application_view_controller.parent_value = $this.current_edit_record.id;
			$this.sub_application_view_controller.parent_edit_record = $this.current_edit_record;
			$this.sub_application_view_controller.parent_view_controller = $this;
			$this.sub_application_view_controller.postInit = function() {
				this.initData();
				TTPromise.resolve( 'initSubApplicationView', 'loadscript' );
			};
		}
	}

	removeEditView() {
		super.removeEditView();

		this.sub_application_view_controller = null;

		this.sub_job_applicant_employment_view_controller = null;
		this.sub_job_applicant_reference_view_controller = null;
		this.sub_job_applicant_location_view_controller = null;

		this.sub_job_applicant_skill_view_controller = null;
		this.sub_job_applicant_license_view_controller = null;
		this.sub_job_applicant_language_view_controller = null;
		this.sub_job_applicant_education_view_controller = null;
		this.sub_job_applicant_membership_view_controller = null;
	}

	getFilterColumnsFromDisplayColumns( column_filter, enable_system_columns ) {
		if ( column_filter == undefined ) {
			column_filter = {};
		}
		column_filter.latitude = true;
		column_filter.longitude = true;
		return this._getFilterColumnsFromDisplayColumns( column_filter, enable_system_columns );
	}
